.likes{
  display: inline-block;
}.gg-eye {
  position: relative;
  display: inline-block;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs,1));
          transform: scale(var(--ggs,1));
  width: 24px;
  height: 18px;
  border-bottom-right-radius: 100px;
  border-bottom-left-radius: 100px;
  overflow: hidden;
  box-sizing: border-box
 }
 
 .gg-eye::after,
 .gg-eye::before {
  content: "";
  display: block;
  border-radius: 100px;
  position: absolute;
  box-sizing: border-box
 }
 
 .gg-eye::after {
  top: 2px;
  box-shadow:
  inset 0 -8px 0 2px,
  inset 0 0 0 2px;
  width: 24px;
  height: 24px
 }
 
 .gg-eye::before {
  width: 8px;
  height: 8px;
  border: 2px solid;
  bottom: 4px;
  left: 8px
 } 
.gg-heart,
.gg-heart::after {
  background-color: red;
 border: 2px solid;
 border-top-left-radius: 100px;
 border-top-right-radius: 100px;
 width: 10px;
 height: 8px;
 border-bottom: 0
}

.gg-heart {
 box-sizing: border-box;
 position: relative;
 -webkit-transform:
 translate(
 calc(-10px / 2 * 1),
 calc(-6px / 2 * 1)
 )
 rotate(-45deg)
 scale(1);
         transform:
 translate(
 calc(-10px / 2 * 1),
 calc(-6px / 2 * 1)
 )
 rotate(-45deg)
 scale(1);
 -webkit-transform:
 translate(
 calc(-10px / 2 * var(--ggs,1)),
 calc(-6px / 2 * var(--ggs,1))
 )
 rotate(-45deg)
 scale(var(--ggs,1));
         transform:
 translate(
 calc(-10px / 2 * var(--ggs,1)),
 calc(-6px / 2 * var(--ggs,1))
 )
 rotate(-45deg)
 scale(var(--ggs,1));
 display: inline-block;
}

.gg-heart::after,
.gg-heart::before {
  background: red;
 content: "";
 display: inline-block;
 box-sizing: border-box;
 position: absolute
}

.gg-heart::after {
 right: -9px;
 -webkit-transform: rotate(90deg);
         transform: rotate(90deg);
 top: 5px
}

.gg-heart::before {
 width: 11px;
 height: 11px;
 border-left: 2px solid;
 border-bottom: 2px solid;
 left: -2px;
 top: 3px
} 
